import { Checkbox } from '@gravity-ui/uikit';
import { Xmark } from '@gravity-ui/icons';
import React from 'react';

export default function RegForm({ setOpen }) {
    return (
        <>
            <div className='form__container'>
                <div className='form__wrapper'>
                    <div className='form__close' onClick={() => setOpen(false)}> <Xmark /></div>
                    <div className='form__enter-system'>  </div>
                    <input type="text" placeholder="Email/" className='form__email' />
                    <input type="password" placeholder="" className='form__password' />
                    <div className='form__remember'><Checkbox size="l"> </Checkbox></div>
                    <div className='form__restore'></div>
                    <button className='form__enter'></button>
                    <button className='form__reg'></button>
                </div >
            </div >
        </>
    );
}